<template>
  <div class="billing row">
    <div class="col-12 mt-3" v-if="isReady">
      <div class="row">
        <div class="col-12 flex-x-between mb-3">
          <div class="flex-align-center">
            <el-button
              v-ripple.400
              circle
              type="primary"
              class="no-width mr-2 flat"
              @click="$router.go(-1)"
            >
              <i class="bx bx-left-arrow-alt"></i>
            </el-button>
            <h5 class="my-0 mx-2">Billing</h5>
            <el-tag type="primary" class="rounded mr-2">{{
              billing.date
            }}</el-tag>
            <el-tag type="primary" class="rounded mr-2">{{
              billing.status
            }}</el-tag>
            <el-tag
              type="success"
              class="rounded mr-2"
              v-if="billing.payment_status == 'Paid'"
              >{{ billing.payment_status }}</el-tag
            >

            <el-tag
              type="danger"
              class="rounded mr-2"
              v-if="billing.payment_status == 'Unpaid'"
              >{{ billing.payment_status }}</el-tag
            >

            <el-tag
              type="warning"
              class="rounded mr-2"
              v-if="billing.payment_status == 'Partially Paid'"
              >{{ billing.payment_status }}</el-tag
            >
          </div>
          <div class="flex-align-center"></div>
        </div>
        <div class="col-lg-8">
          <div class="box">
            <p class="mt-0 mb-2 text-bold">Details</p>
            <div class="flex-x-between w-100">
              <p>Plan :</p>
              <p>
                {{ billing.selected_plan.name }}
              </p>
            </div>
            <div class="b-divider my-3"></div>
            <div class="flex-x-between w-100">
              <p>Number of months :</p>
              <p>
                {{ billing.number_of_months }}
              </p>
            </div>
            <div class="b-divider my-3"></div>
            <div class="flex-x-between w-100">
              <p>Price per month :</p>
              <p>{{ billing.price }}MMK</p>
            </div>
            <div class="b-divider my-3"></div>
            <div class="flex-x-between w-100">
              <p>Total :</p>
              <p>{{ billing.total }}MMK</p>
            </div>
            <div class="b-divider my-3"></div>
            <div class="flex-x-between w-100">
              <p>Payment status :</p>
              <p>{{ billing.payment_status }}</p>
            </div>
          </div>
          <div class="box mt-3">
            <div class="flex-x-between">
              <p class="mt-0 mb-2 text-bold">Payment history</p>
              <p
                class="mt-0 mb-2 primary hover-underline pointer"
                @click="openPayment = true"
                v-if="billing.payment_status !== 'Paid'"
              >
                Add payment
              </p>
            </div>
            <div
              class="w-100"
              v-for="(payment, pIndex) in billing.payments"
              :key="pIndex"
            >
              <p class="my-2">
                Paid {{ payment.amount }}MMK on {{ payment.date }}
                <span v-if="payment.payment_method">
                  via
                  {{ payment.payment_method.name }}
                  ({{ payment.payment_method.payment_number }})</span
                >.
              </p>
              <div class="media-box">
                <label for="" class="custom-label">Screenshots</label>
                <div class="image-list">
                  <div
                    v-for="(image, imgIndex) in payment.images"
                    :key="image.image"
                    class="pointer light-box-img"
                  >
                    <img
                      :src="mediaUrl + image.image"
                      @click="(index = imgIndex), (activePaymentIndex = pIndex)"
                      alt=""
                    />
                  </div>
                </div>
              </div>
              <div
                class="b-divider"
                v-if="pIndex !== billing.payments.length - 1"
              ></div>
            </div>
          </div>
        </div>

        <div class="col-lg-4 pl-lg-0 mt-3 mt-lg-0">
          <div class="mb-3 box">
            <label class="custom-label mt-2 mb-0">Note</label>
            <p>{{ billing.note }}</p>
          </div>
        </div>
      </div>
    </div>

    <CoolLightBox
      v-if="billing.payments[0] && billing.payments[0].images.length > 0"
      :items="imagesListGenerator(billing.payments[activePaymentIndex].images)"
      :useZoomBar="true"
      :index="index"
      @close="index = null"
    >
    </CoolLightBox>

    <el-dialog
      class="default-dialog"
      :show-close="false"
      width="700px !important"
      :visible.sync="openPayment"
    >
      <template #title>
        <div class="d-flex align-items-center justify-content-between">
          <h5 class="my-0">Make a payment</h5>
          <div class="d-flex align-items-center"></div>
        </div>
      </template>

      <div class="con-content">
        <div class="row">
          <div class="col-12">
            <div class="row px-2">
              <div
                class="col-4 px-2"
                v-for="paymentMethod in paymentMethods"
                :key="paymentMethod.id"
              >
                <div
                  class="payment-box pointer"
                  @click="payment.payment_method = paymentMethod"
                  :class="
                    paymentMethod.name == payment.payment_method.name &&
                    'active'
                  "
                >
                  <p class="mb-3 font-weight-bold">{{ paymentMethod.name }}</p>
                  <p>{{ paymentMethod.payment_number }}</p>
                </div>
              </div>
            </div>
          </div>
          <div class="col-12 mb-3">
            <label for="" class="custom-label mt-1 md-text">Screenshots</label>

            <file :multiple="true" @completed="setImages" />
          </div>
          <div class="col-6 pr-1">
            <div class="w-100">
              <label for="" class="custom-label md-text">Date</label>
              <el-date-picker
                class="w-100"
                v-model="payment.date"
                type="date"
                format="dd/MM/yyyy"
                value-format="dd/MM/yyyy"
                placeholder="Pick a date"
              >
              </el-date-picker>
            </div>
          </div>
          <div class="col-6 pl-1">
            <label for="Amount" class="custom-label">Amount</label>
            <el-input
              name="Amount"
              type="number"
              class="w-100"
              v-model="payment.amount"
            />
          </div>
        </div>
      </div>

      <template #footer>
        <div class="d-flex align-items-center justify-content-end">
          <el-button
            @click="openPayment = false"
            class="my-0 flat mr-0"
            type="dark"
            v-ripple.400
          >
            Cancel
          </el-button>
          <el-button
            class="my-0 mr-0"
            v-ripple.400
            type="primary"
            @click="addPayment"
            :loading="adding"
            :disabled="disabledAddPayment"
          >
            Done
          </el-button>
        </div>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapState } from "vuex";
import { url, mediaUrl } from "../../../../core/constant/data";
import { file } from "../../../common";
export default {
  data() {
    return {
      adding: false,
      url: url,
      index: null,
      activePaymentIndex: 0,
      openPayment: false,
      mediaUrl: mediaUrl,
      paymentMethods: [],
      saving: false,
      payment: {
        images: [],
        payment_method: {
          name: "",
        },
        date: null,
        amount: 0,
      },
      startEditing: false,
      billing: {
        store: null,
        selected_plan: null,
        payments: [
          {
            images: [],
            date: null,
            amount: 0,
          },
        ],
        payment_method: null,
        subtotal: null,
        total: null,
        status: "Pending",
        payment_status: "Unpaid",
        number_of_months: null,
      },
      selectedPackage: null,
      disabledAddPayment: true,
    };
  },
  watch: {
    selectedPackage: {
      handler: function (val) {
        this.billing.price = val.price;
        this.billing.number_of_months = val.monthQuantity;
        this.billing.total = val.price * val.monthQuantity;
      },
      deep: true,
    },
    payment: {
      handler: function (val) {
        if (val.payment_method.name == "" || val.amount == 0) {
          this.disabledAddPayment = true;
        } else {
          this.disabledAddPayment = false;
        }
      },

      deep: true,
    },
  },
  created() {
    if (!this.$route.query.bid) {
      this.$router.push({ name: "Billings" });
    }
    this.payment.date = this.$moment().format("DD/MM/YYYY");
    this.$store.commit("setIsReady", false);
    this.getPaymentMethods();
    this.getBilling(this.$route.query.bid);
  },
  mounted() {},
  computed: {
    ...mapState(["isReady"]),
    ...mapGetters(["checkIsReady"]),
  },
  components: {
    file,
  },
  methods: {
    getPaymentMethods() {
      this.$axios
        .get("payment-methods/")
        .then((response) => {
          this.paymentMethods = response.data;
        })
        .catch((err) => {
          this.$noti.display({ message: err, status: "error", color: "error" });
        });
    },
    addPayment() {
      this.adding = true;
      this.billing.payments.push(this.payment);
      this.checkPayment();
      this.$axios
        .post("billing-payment/", {
          bid: this.billing.id,
          payment_status: this.billing.payment_status,
          payment: this.payment,
        })
        .then(() => {
          this.adding = false;
          this.openPayment = false;
        })
        .catch((err) => {
          this.$noti.display({ message: err, status: "error", color: "error" });
          this.adding = false;
          this.openPayment = false;
        });
    },
    checkPayment() {
      var totalPaid = this.billing.payments.reduce((total, payment) => {
        return total + parseInt(payment.amount);
      }, 0);
      if (totalPaid == this.billing.total || totalPaid > this.billing.total) {
        this.billing.payment_status = "Paid";
      } else if (totalPaid < this.billing.total) {
        this.billing.payment_status = "Partially Paid";
      } else if (totalPaid == 0) {
        this.billing.payment_status = "Unpaid";
      }
    },
    setImages(images) {
      this.payment.images = images;
    },
    getBilling(bid) {
      this.$axios
        .get("billing/?bid=" + bid)
        .then((response) => {
          this.billing = response.data;
          this.$store.commit("setIsReady", true);
        })
        .catch((err) => {
          this.$noti.display({ message: err, status: "error", color: "error" });
        });
    },
    imagesListGenerator(imgs) {
      var images = [];
      imgs.forEach((img) => {
        images.push(this.mediaUrl + img.image);
      });
      return images;
    },
  },
};
</script>
<style>
.image-list {
  display: flex;
  overflow-x: auto;
}
.image-list img {
  width: 150px;
  margin-right: 5px;
  border-radius: 5px;
  object-fit: cover;
  height: 150px;
  margin-bottom: 7px;
}
.payment-box {
  border-radius: 6px;
  padding: 10px 15px;
  background: rgba(var(--primary), 0.05);
  border: 2px solid transparent;
}
.payment-box.active {
  border: 2px solid rgba(var(--primary), 1);
}
</style>